@charset "utf-8";
@import "vars";
@import '../../node_modules/bulma/sass/utilities/_all';

@include until($desktop){
    .container.background{
        margin: 0 !important;
    }
}

.subtitle {
    padding-left: 0;
}

.container {
    display: flex;
    flex-direction: column;
}
.background {
    color: #bfbfbf;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 0.5em;
    border-radius: 6px;
    margin: 0 auto;
    min-width: 70%;
}
.google-maps {
    flex-grow: 1;
    border: 0;
    width: 100%;
    filter: grayscale(100%) invert(100%);
    margin-bottom: 1em;
}
a:link, a:visited {
    color: white;
}
a:hover {
    color: #363636;
}
a{
    user-select: all !important;
}
a::selection {
    background: #FFBE74 !important;
}
.rrss {
    display: inline-block;
    width: 32px;
    height: 32px;
    text-indent: -99999px;
    margin-right: 10px;
    background-size: cover !important;
    -webkit-transition: background-position 0.2s ease;
    transition: background-position 0.2s ease;
}
.rrss.fb { background: url("../img/fb_.png") bottom; }
.rrss.tw { background: url("../img/tw_.png") bottom; }
.rrss.in { background: url("../img/in_.png") bottom; }
.rrss.yt { background: url("../img/yt_.png") bottom; }
.rrss.ig { background: url("../img/ig_.png") bottom; }
.rrss:hover { background-position: 0 0; }